.personal-office{
  &__main-row{
    display: grid;
    grid-template-columns: minmax(180px, 290px) minmax(620px, 820px);
    grid-column-gap: 20px;
    grid-row-gap: 24px;
    @media screen and (max-width: 815px){
      grid-template-columns:1fr;
    }
  }
  &__col{
    @media screen and (max-width: 815px){
      overflow: hidden;
    }
  }
  &__wr-nav{
    width: 100%;
    max-width: 256px;
    @media screen and (max-width: 815px){
      max-width: 100%;
      //overflow: hidden;
    }
  }
  &__title{
    font-size: 32px;
    line-height: 32px;
    color: #FFFFFF;
    font-family: $ff-semi-bold;
    letter-spacing: -0.01em;
    @media screen and (max-width: 970px){
      font-size: 26px;
      line-height: 26px;
    }
  }
  &__container-nav{
    @media screen and (max-width: 815px){
      position: relative;
      top: 10px;
      overflow-x: auto;
    }
  }
  &__nav{
    width: 100%;
    max-width: 242px;
    margin-top: 40px;
    @media screen and (max-width: 815px){
      margin-top: 24px;
      max-width: 100%;
      display: flex;
      align-items: center;
      position: relative;
      top: -10px;
    }
    li{
      padding: 8px;
      border-radius: 8px;
      &:not(:first-child){
        margin-top: 4px;
      }
      &:hover{
        background-color: #67A511;
      }
      @media screen and (max-width: 815px){
        padding: 5px;
        flex-shrink: 0;
        &:not(:last-child){
          margin-right: 15px;
        }
      }
      p{
        font-size: 16px;
        line-height: 24px;
        font-family: $ff-medium;
        color: #FFFFFF;
      }
      a{
        display: flex;
        align-items: center;
        p{
          font-size: 16px;
          line-height: 24px;
          font-family: $ff-medium;
          color: #FFFFFF;
        }
      }
      &.active{
        background-color: #A3D562;
        a{
          p{
            color: #000000;
          }
        }
        .personal-office__nav-icon{
          svg{
            path{
              stroke:#000;
            }
          }
        }
        &:hover{
          background-color: #A3D562;
        }
      }

    }
  }
  &__nav-icon{
    margin-right: 8px;
  }
  &__body{
    overflow: hidden;
    background: #252527;
    box-shadow: 0 33px 80px rgba(0, 0, 0, 0.03);
    border-radius: 24px;
    padding: 40px;
    @media screen and (max-width: 970px){
      padding: 32px;
    }
    @media screen and (max-width: 620px){
      padding: 25px 20px;
      width: calc(100% + 32px);
      position: relative;
      left: 50%;
      transform: translateX(-50%);
    }

    @media screen and (max-width: 470px){
      padding: 16px;
    }

  }
  &__body-top{
    position:relative ;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  &__user{
    display: flex;
    align-items: center;
    @media screen and (max-width: 625px){
      flex-direction: column;
      align-items: flex-start;
    }
  }
  &__user-foto{
    width: 120px;
    height: 120px;
    border-radius: 50%;
    overflow: hidden;
    flex-shrink: 0;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    @media screen and (max-width: 1020px){
      width: 104px;
      height: 104px;
    }
    @media screen and (max-width: 470px){
      width: 80px;
      height: 80px;
    }

  }
  &__user-form{
    @media screen and (max-width: 625px){
      margin-top: 52px;
    }
  }
  &__user-wr-info{
    margin-left: 40px;
    @media screen and (max-width: 1020px){
      margin-left: 30px;
    }
    @media screen and (max-width: 625px){
      position: absolute;
      bottom: -52px;
      margin-left: 0;
      margin-top: 16px;
    }

  }
  &__user-name{
    font-size: 20px;
    line-height: 28px;
    color: #FFFFFF;
    font-family: $ff-bold;
    @media screen and (max-width: 1020px){
      font-size: 16px;
      line-height: 24px;
    }
  }
  &__user-id{
    margin-top: 4px;
    font-size: 14px;
    line-height: 18px;
    color: #686D82;
    font-family: $ff-medium;
    @media screen and (max-width: 1020px){
      font-size: 12px;
      line-height: 16px;
    }
  }
  &__row-scored{
    display: flex;
    justify-content: space-between;
    align-items: center;
    //width: 100%;
    //max-width: 174px;
  }
  &__wr-scored{
    display: flex;
  }
  &__scored-item{
    display: flex;
    &:not(:last-child){
      margin-right: 12px;
    }
    p{
      font-size: 14px;
      line-height: 18px;
      font-family: $ff-bold;
      margin-right: 6px;
      color: #FFFFFF;
    }
  }
  &__scored-icon{
    width: 16px;
  }
  &__status-user{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 40px;
    @media screen and (max-width: 1020px){
      margin-left: 30px;
    }
    @media screen and (max-width: 480px){
      margin-left: 20px;
    }

  }
  &__user-form{
    //margin-top: 32px;
  }
  &__user-form-row{
    margin-right: -12px;
    margin-left: -12px;
    display: flex;
    flex-wrap: wrap;
  }
  &__user-form-col{
    margin-left: 12px;
    margin-right: 12px;
    width: calc(50% - 24px);
    flex-grow: 1;
    min-width: 226px;
    margin-top: 32px;
    &.confirmd{
      .personal-office__user-form-input-confirmed{
        display: block;
      }
    }
    @media screen and (max-width: 435px){
      margin-top: 16px;
    }
    &.active{
      .personal-office__user-form-label{
        color: #fff;
      }
      .personal-office__user-form-input{
        border-color: #fff;
      }
    }
  }
  &__user-form-change{
    width: max-content;
    cursor: pointer;
    margin-top: 12px;
    font-size: 16px;
    line-height: 24px;
    color: #BEC6D1;
    border-bottom: 1px solid #BEC6D1;
    &:hover{
      color: #b3bbc6;
    }

  }
  &__user-form-label{
    font-size: 12px;
    line-height: 16px;
    color: #737373;
    font-family: $ff-medium;
    margin-bottom: 4px;
  }
  &__user-form-input{
    overflow: hidden;
    width: 100%;
    height: 48px;
    border: 1px solid #DEDEDE;
    border-radius: 24px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-right: 24px;
    input{
      width: 82%;
      height: 100%;
      font-size: 16px;
      line-height: 24px;
      color: #686D82;
      padding: 12px 16px;
      background-color: transparent;
      outline: none;
      &:not(:read-only){
        &:focus{
          color:#fff;
        }
      }
    }
  }
  &__user-form-input-confirmed{

  }
  &__user-form-subscription-em{
    flex-shrink: 0;
    width: 32px;
  }
  &__user-form-subscription {
    display: flex;
    @media screen and (max-width: 515px){
      order: -1;
    }
    label {
      max-width: 275px;
      display: block;
      @media screen and (max-width: 515px){
        max-width: 100%;
      }
      p{
        display: inline;
        color: #BEC6D1;
        //font-size: 16px;
        //line-height: 24px;
        font-family: $ff-medium;
      }

      cursor: pointer;
      color: #BEC6D1;
      font-size: 16px;
      line-height: 24px;
      font-family: $ff-medium;

      &:before {
        height: 16px;
        width: 16px;
        border: 1px solid #686d82;
        box-sizing: border-box;
        left: 0;
        top: 2px;
      }

      &:after {
        height: 6px;
        width: 10px;
        border-left: 2px solid #246900;
        border-bottom: 2px solid #246900;
        transform: rotate(-59deg);
        left: 3px;
        top: 6px;
      }
    }

    input[type="checkbox"]:checked + label::before {
      border-color: #fff;
    }
    input[type="checkbox"]:checked + label::after {
      border-color: #fff;
    }

    a {
      padding-left: 6px;
      color: #67A511;
      //font-size: 16px;
      //line-height: 24px;
      //font-family: $ff-medium;
    }

  }
  &__user-form-wr-btn{
    margin-top: 40px;
    display: flex;
    align-items: center;
    @media screen and (max-width: 515px){
      flex-direction: column;
      align-items: flex-start;
    }
  }
  &__user-form-btn{
    margin-right: 52px;
    cursor:pointer;
    padding: 10px 24px;
    background: #F1F2F4;
    border-radius: 24px;
    color: #BEC6D1;
    font-size: 20px;
    line-height: 28px;
    font-family: $ff-medium;
    border: none;
    &:hover{
      box-shadow: 0 0 9px 1px rgba(255, 255, 255, 0.24);
    }
    @media screen and (max-width: 515px){
      width: 100%;
      margin-right: 0;
      margin-top: 32px;
    }
    @media screen and (max-width: 435px){
      //width: 100%;
      //margin-top: 32px;
    }
  }

  &__header-user{
    cursor:pointer;
    flex-shrink: 0;
    width: 32px;
    height: 32px;
    margin-left: 27px;
    border-radius: 50%;
    overflow: hidden;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      @media screen and (max-width: 840px){
        //display: none;
      }
    }
    svg{
      display: none;
      @media screen and (max-width: 840px){
        //display: block;
      }
    }
  }
  &__alarm{
    display: none;
    position: absolute;
    top: 0;
    right: 0;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: #FF0000;
    &.active{
      display: block;
    }
  }
  &__notifications{
    display: none;
    position: absolute;
    right: 20px;
    top: 51px;
    z-index: 200;
    width: 100%;
    max-width: 350px;
    background: #252527;
    box-shadow: 0 22px 80px rgba(0, 0, 0, 0.21);
    border-radius: 24px;
    padding-top: 20px;
    padding-bottom: 20px;
    &.active{
      display: block;
    }
    @media screen and (max-width: 470px){
      top: 0;
      max-width: 98%;
      right: 50%;
      transform: translateX(50%);
      height: 99vh;

    }
  }

  &__notifications-top{
    padding-left: 20px;
    padding-right: 24px;
    padding-bottom: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    p{
      font-size: 24px;
      line-height: 26px;
      color: #FFFFFF;
      font-family: $ff-semi-bold;
    }
  }
  &__status{
    &.status-checked{
      .personal-office__notifications-icon{
        background-color: #82BF00;
        background-image: url("data:image/svg+xml,%3Csvg width='8' height='6' viewBox='0 0 8 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M7.62796 0.205535C7.79068 0.368253 7.79068 0.632072 7.62796 0.794791L3.04463 5.37812C2.88191 5.54084 2.61809 5.54084 2.45537 5.37812L0.372039 3.29479C0.20932 3.13207 0.20932 2.86825 0.372039 2.70553C0.534757 2.54282 0.798576 2.54282 0.961294 2.70553L2.75 4.49424L7.03871 0.205535C7.20142 0.0428165 7.46524 0.0428165 7.62796 0.205535Z' fill='white'/%3E%3C/svg%3E");
      }
    }
