  }
  &__notifications-list{
    max-height: 440px;
    overflow: auto;
    overflow-y: auto;
    scrollbar-color: #fff #caccdf;
    scrollbar-width: thin;
    @media screen and (max-width: 470px){
      max-height: 96%;

    }
    &::-webkit-scrollbar {
      width: 4px;
      height: 4px;
    }

    &::-webkit-scrollbar-button {
      width: 0;
      height: 0;
    }

    &::-webkit-scrollbar-thumb {
      background: #fff;
      border: 0 #fff;
      border-radius: 50px;
    }

    &::-webkit-scrollbar-track {
      background: #686D82;
      border: 0 #fff;
      border-radius: 50px;
    }

    &::-webkit-scrollbar-corner {
      background: transparent;
    }
    li{
      cursor:pointer;
      padding: 12px 10px 12px 24px;
      &:hover{
        background-color: #313137;
      }
      &:not(:first-child){
        margin-top: 4px;
      }



    }
  }
  &__notifications-wr-not{
    margin-left: 12px;
  }
  &__notifications-info{
    display: flex;
  }
  &__notifications-icon{
    margin-top: 2px;
    width: 18px;
    height: 18px;
    flex-shrink: 0;
    border-radius: 50%;
    background-position: center;
    background-repeat: no-repeat;
  }
  &__notifications-title{
    font-size: 16px;
    line-height: 24px;
    color: #FFFFFF;
    font-family: $ff-semi-bold;

  }
  &__notifications-data{
    margin-top: 4px;
    font-size: 14px;
    line-height: 18px;
    color: #686D82;
    font-family: $ff-medium;
  }

  &__empty-container{
    display: none;
    position: absolute;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    background-color: transparent;
    z-index: 10;
    &.active{
      display: block;
    }
  }
  &__notifications-close{
    cursor: pointer;
  }

//  

  &__flex-row{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__h2-title{
    font-size: 24px;
    line-height: 26px;
    font-family: $ff-semi-bold;
    color:#fff;
    @media screen and (max-width: 580px){
      font-size: 20px;
      line-height: 24px;
    }
  }
  &__container-tasks{
    margin-top: 32px;
  }
  &__group-tasks{
    position: relative;
    &:first-child{
      .personal-office__tasks-rows{
        max-height: 1000px;
      }
    }
    &:not(:first-child){
      margin-top: 46px;
      &:before{

      }
    }
    &:not(:last-child){
      &:after{
        content: "";
        display: block;
        position: relative;
        width: 100%;
        height: 1px;
        background-color: #464854;
        transform: translateY(23px);
      }
    }
    &.active{
      &:after{
        display: none;
      }
      .personal-office__tasks-arrow{
        transform: rotate(180deg);
      }
    }

  }
  &__tasks-arrow{
    transition-duration: .3s;
  }
  &__group-wr-tasks-title{
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    //min-height: 50px;
  }
  &__tasks-title{
    font-size: 20px;
    line-height: 28px;
    font-family: $ff-bold;
    color:#fff;
    margin-right: 10px;
    @media screen and (max-width: 580px){
      font-size: 16px;
      line-height: 20px;
    }

  }
  &__tasks-description{
    margin-top: 9px;
    p{
      font-size: 14px;
      line-height: 18px;
      color: #FFFFFF;
      font-family: $ff-medium;
      &:not(:first-child){
        margin-top: 8px;
      }

    }
  }

  &__tasks-rows{
    max-height: 0;
    overflow: hidden;
    transition-duration: .5s;
  }
  &__tasks-rows-wr{
    margin-top: 24px;
    margin-bottom: 9px;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 16px;
    @media (min-width: 815px) and (max-width: 1020px) {
      grid-template-columns: repeat(2,1fr);
    }
    @media screen and (max-width: 740px){
      grid-template-columns: repeat(2,1fr);
    }
    @media screen and (max-width: 530px){
      grid-template-columns: repeat(1,1fr);
    }



  }
  &__tasks{
    overflow: hidden;
    position: relative;
