      &:before{
        content: "";
        display: block;
        position: absolute;
        height: 19px;
        width: 19px;
        border-radius: 50%;
        border: 2px solid #fff;
        background-color: #464854;
        left: -30px;
        transform: translate(-100%,-55%);
        top: 50%;
        z-index: 2;
        @media screen and (max-width:670px){
          right: -10%;
          left: unset;
          transform: translate(64%,-55%);
        }
        @media screen and (max-width:383px){
          transform: translate(52%,-55%);
        }
      }
      &:after{
        content: "";
        display: block;
        position: absolute;
        height: 2px;
        width: 38px;

        background: linear-gradient(-270deg, #FFFFFF 0%, #252527 51.56%);
        left: 0;
        transform: translate(-100%,-64%);
        top: 50%;
