          width: 10%;
          left: unset;
          right: 0;
          transform: translate(100%,-64%);
          background: linear-gradient(270deg, #FFFFFF 0%, #252527 51.56%);
        }
      }
      &:first-child{
        &:before{
          @media screen and (min-width:670px){
            border-color:#A3D562;
          }
        }
        &:after{
          @media screen and (min-width:670px){
            background: linear-gradient(-270deg, #A3D562 0%, #A3D562 0%, #252527 53.88%);
